<script>
import FingerprintJs from '@fingerprintjs/fingerprintjs'
import * as _ from 'lodash';

export default {
  name: "AndroidAds",
  data(){
    return {
      showPanel:false,
      auadid: '',
      ct: 1,
      cl: 1,
      isPc: this.$isPc(),
      androidDownloadLink: ''
    }
  },
  created() {
    console.log(this.isPc)
    if (this.$route.query.ct || this.$route.query.cl) {
      this.ct = this.$route.query.ct
      this.cl = this.$route.query.cl
      // this.$router.replace({query: _.omit(this.$route.query, ['ct', 'cl'])})
    }
    this.getAndroidDownloadLink()

    // event fire
    this.$eventFire.viewContent()
  },

  methods:{
    getAndroidDownloadLink() {
      const that = this
      this.$request.auGetAppDownloadLink('android', function (data) {
        if ((data.status === 200 || data.status === 201) && data.data) {
          that.androidDownloadLink = data.data.url
        }
      })
    },
    clickShowPanel(){
      this.showPanel = !this.showPanel
    },
    copyAuadid() {
      let input = document.createElement("input");
      input.value = this.auadid;
      document.body.appendChild(input);
      input.select(); // 选择对象
      document.execCommand("Copy"); // 执行浏览器复制命令
      input.className = "input";
      input.style.display = "none";

      if (navigator.clipboard) {
        navigator.clipboard.writeText(this.auadid).then(function() {
          console.log('Async: Copying to clipboard was successful!');
        }, function(err) {
          console.error('Async: Could not copy text: ', err);
        });
      }
    },
  },

  mounted() {
    // To avoid sending request before loading finger print result. Moving those logics to component level.
    (async () => {
      // Wait The FingerprintJS agent is ready.
      const agent = await FingerprintJs.load();

      const result = await agent.get();
      result.components.canvas = '{}'
      const jsonResult = JSON.stringify(result);
      const that = this
      this.$request.postUploadAdsFpRequest(
          jsonResult,
          function (data){
            if (data.data && data.data.auadid){
              that.auadid = data.data.auadid
              // const el = document.createElement('textarea');
              // el.value =  data.data.auadid;
              // document.body.appendChild(el);
              // el.focus();
              // el.select();
              // document.execCommand('copy');
              // document.body.removeChild(el);

              // navigator.clipboard.writeText(data.data.auadid).then(function() {
              //   console.log('Async: Copying to clipboard was successful!');
              //   that.$message.info(`Async: Copying to clipboard was successful!`);
              //
              // }, function(err) {
              //   console.error('Async: Could not copy text: ', err);
              //   that.$message.info(`Async: Could not copy text: ${err}`);
              //
              // });
            }
          }
      )
    })();
  },
}
</script>

